
define('bs-http-plugin/bs-pd',['bs-http-plugin/base-pluginvoke'], function(BasePluginvoke,Socket) {

    /**
     * @desc  PD  'bs-http-plugin/bs-pd'
     * @class
     * @classdesc
     * @name bs-pd
     * @extends base-pluginvoke
     */
    var Pd = BasePluginvoke.extend({
        /**
         * @description dll
         * @memberOf bs-pd
         * @instance
         * @example
         * module:'print'
         */
        module:'pd',
        /**
         * @description 
         */
        functions:[
            /**
             * @function doPd
             * @instance
             * @memberOf bs-pd
             * @description CA
             * @param {object} options
             * @returns {string} options.aUrl
             * @returns {string} options.aPdaName
             * @returns {string} options.aOrgCode
             * @returns {string} options.aPdId
             * @returns {string} options.aPdCode
             *
             * @example
             * var instance = Pd.getInstance();
             * instance.expPd({
             * aUrl: '',
             * aPdaName: '',
             * aOrgCode: '',
             * aPdId: '',
             * aPdCode: ''
			 * }).done(function(data){
			 * 		//TODO success
			 * }).fail(function(){
			 * 		//TODO error
			 * })
             */
            'expPd',


            /**
             * @function impPd
             * @instance
             * @memberOf bs-pd
             * @description CA
             * @returns {string} options.aUrl
             * @returns {string} options.aPdaName
             * @returns {string} options.aOrgCode
             * @returns {object}
             *
             * @example
             * var instance = Pd.getInstance();
             * instance.impPd({
             * aUrl: '',
             * aPdaName: '',
             * aOrgCode: '',
			 * }).done(function(data){
			 * 		//TODO success
			 * }).fail(function(){
			 * 		//TODO error
			 * })
             */
            'impPd'
        ],
        Statics:{
            /**
             * @static
             * @memberOf bs-pd
             * @function getInstance
             * @param {object} op
             * @param {object} op.config dll
             * @param {string} op.config.appId Id(<span style="color:red"></span>)
             * @example
             *         var instantce = Pd.getInstance({
             *          config:{
             *              appId:'123'
             *          }
             *         });
             */
            getInstance:function(op){
                if (!this.instance){
                    this.instance =new Pd(op);
                }
                return this.instance;
            }
        }
    });
    return Pd;
});
