define('app/widgets/app-widget',["app/core/app-base",'app/core/app-jquery'], function(Base,$) {
    // Widget
    // ---------
    // Widget  DOM  View 
    // Widget  attributes  properties events
    //  methodsWidget 
    /**
     * Widget  DOM  View 
     * @class
     * @name Widget
     * @extends Base
     * @classdesc 
     */
  var Widget = Base.extend({
        // config  properties
  		//, 'template', 'model', 'events'
      	 propsInAttrs: ['element','template', 'model', 'events'],

        //  widget  DOM 
      	element: null,

        // 
        template: '<div></div>',

        // 
       // model: null,

        // 
        //   {
        //     'mousedown .title': 'edit',
        //     'click {{attrs.saveButton}}': 'save'
        //     'click .open': function(ev) { ... }
        //   }
        events: null,

        // 
        attrs: {
            // 
          //  parentNode: document.body,

            //  data-api 
            //'data-api': true
        },
        // 
        //  attrs --  properties --  events -- 
        initialize: function(config) {
            //this.cid = uniqueCid();
            //  Base 
            this.initAttrs(config);
            //  Widget 
            // 
             this.initProps();
             this.setup();
             //this.delegateEvents();
        },

        //  this.element
        //  this.element
        parseElement: function() {
            var element = this.element;
            if (element) {
                this.element = $(element);
            }
            //  element  template 
            else if (this.get('template')) {
                this.parseElementFromTemplate();
            }

            //  DOM 
            if (!this.element || !this.element[0]) {
                throw 'element is invalid';
            }
        },

        //  this.element
          //  this.element
        parseElementFromTemplate: function() {
            this.element = $(this.get('template'));
        },
        //  this.element  data-*  this.dataset
        //  data-action 
        _parseDataAttrs: function() {
           
        },
        //  properties 
        initProps: function() {
        },
        // 
        delegateEvents: function(events, handler) {
           
        },
        addEvents : function(o){
            var me = this;
            me.events = me.events || {};
            if (typeof o == 'string') {
                var a = arguments,
                    i = a.length;
                while(i--) {
                    me.events[a[i]] = me.events[a[i]] || true;
                	if (this.get(a[i])){
                	 this.off(a[i]);
                	 this.on(a[i],this.get(a[i]),this);
                	}
                }
            } else {
            	for(var evnetName in o){
            		this.off(evnetName);
            		this.on(evnetName,o[evnetName],this);
            	}
                $.extend(me.events, o);
            }
        },
        // 
        undelegateEvents: function(eventKey, handler) {
        	
        	
        },
        // 
        setup: function() {
        },
        //  widget 
        //  DOM 
        //  `return this`
        render: function() {
           
        },
        destroy: function() {
          //  this.undelegateEvents();
            Widget.superclass.destroy.call(this);
        }
    });

    return Widget;
   
});

